.VehicleDetails-container {
    max-width: 900px;
    margin: 2rem auto;

    font-family: sans-serif;
  }
  
  h2 {
    margin-bottom: 1rem;
  }
  
  .form-card {
    background-color: #dcdcdc;
    padding: 2rem;
    border-radius: 8px;
  }
  
  .calculation-card {

    font-size: 20px;
    
  }

  .calculation-card p {
    margin-top: 20px;
  }


  .form-group input {
    padding: 0.5rem;
    font-size: 1rem;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
  }
  
  .button-containers {
    margin-top: 1rem;
    text-align: right;
  }
  
  .submit-btn, .back-btn {
    padding: 0.6rem 1.5rem;
    background-color: #ccc;
    border: none;
    border-radius: 20px;
    font-size: 1rem;
    cursor: pointer;
    margin: 5px;
  }
  
  .submit-btn:hover, .back-btn:hover {
    background-color: #aaa;
  }